<template>
  <!-- Video -->
  <div class="col-md-6 col-12 py-0 mb-3">
    <div class="plyr__video-embed js-player">
      <iframe
        :src="
          'https://www.youtube.com/embed/' +
          youtubeId +
          '?origin=https://plyr.io&amp;iv_load_policy=3&amp;modestbranding=1&amp;playsinline=1&amp;showinfo=0&amp;rel=0&amp;enablejsapi=1'
        "
        allowfullscreen
        allowtransparency
        allow="autoplay"
      ></iframe>
    </div>
  </div>
  <div class="col-md-5 col-12 row d-flex align-items-baseline gy-4 mt-0">
    <div class="col-12 m-0">
      <b>{{ item.primary.item_title }}</b>
    </div>
    <div class="col-12">
      <prismic-rich-text :field="item.primary.item_description" />
    </div>
    <div class="col-12 m-0">
      <h4>£{{ item.primary.price }}</h4>
    </div>
    <div class="col-12">
      <button
        :data-item-name="item.primary.item_title"
        :data-item-id="item.id"
        :data-item-price="item.primary.price"
        :data-item-image="item.primary.image.url"
        :data-item-file-guid="item.primary.snipcart_digital_good_guid"
        :data-item-description="item.primary.item_description[0].text + '..'"
        :data-item-shippable="false"
        type="button"
        class="btn contact-link btn-dark snipcart-add-item"
      >
        <small>Buy Now</small>
      </button>
    </div>
  </div>
</template>
<script>
import Plyr from "plyr";
import "plyr/dist/plyr.css";
export default {
  props: {
    item: Object,
    index: Number,
  },
  data() {
    return {};
  },

  computed: {
    youtubeId() {
      let regExp =
        /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
      let match = this.item.primary.video_embed.embed_url.match(regExp);
      return match && match[7].length == 11 ? match[7] : false;
    },
  },
  mounted() {
    const players = Array.from(document.querySelectorAll(".js-player")).map(
      (p) =>
        new Plyr(p,{
          controls: [
            "play-large",
            "play",
            "progress",
            "current-time",
            "fullscreen",
          ],
          fullscreen: {
            enabled: true,
          },
        })
    );

    window.player = players;
  },
};
</script>
<style scoped>
iframe {
  min-height: inherit;
}
.js-player {
  border-radius: 8px;
}
</style>